// eslint-disable-next-line no-unused-vars
function GadgetSummary (selectedOption, currentUser, pivotTableType, translations) {
  var getItemsByKey = function (key, labelSuffix) {
    var result = []
    for (var i = 0; i < selectedOption.length; i++) {
      if (selectedOption[i].key == key && (labelSuffix == null || labelSuffix == /[^.]*$/.exec(selectedOption[i].label))) {
        result.push(selectedOption[i])
      }
    }
    return result
  }

  var getItemValue = function (items) {
    var result = ''
    for (var i = 0; i < items.length; i++) {
      if (result.length > 0) {
        result += ', '
      }
      result += items[i].labelParam
    }
    return result
  }

  var createSummaryItem = function (text, styleClass) {
    return {
      text: text,
      styleClass: styleClass
    }
  }

  this.getSummaryItems = function () {
    var result = []
    result.push(createSummaryItem(translations['Summary '], 'grey'))

    var allUsers = getItemValue(getItemsByKey('allUsers'))
    var value_group = getItemValue(getItemsByKey('group'))
    var value_project = getItemValue(getItemsByKey('filterOrProjectId', 'project'))
    var value_filter = getItemValue(getItemsByKey('filterOrProjectId', 'filter'))
    var value_startDate = getItemValue(getItemsByKey('startDate'))
    var value_endDate = getItemValue(getItemsByKey('endDate'))
    var value_groupedBy = getItemValue(getItemsByKey('groupByField'))
    var value_categorizeBy = getItemValue(getItemsByKey('categorizeByField'))

    if (allUsers && ['Timesheet', 'Calendar', 'PieChart'].indexOf(pivotTableType) >= 0 || !currentUser) {
      result.push(createSummaryItem(translations['for all users'], 'grey'))
    } else if (value_group) {
      result.push(createSummaryItem(translations['for groups'], 'grey'))
      result.push(createSummaryItem(value_group))
    } else if (currentUser) {
      result.push(createSummaryItem(translations['for user'], 'grey'))
      result.push(createSummaryItem(currentUser.displayName))
    }
    if (value_project != null && value_project.length != '') {
      if (result.length > 1) {
        result.push(createSummaryItem(translations['and'], 'grey'))
      } else {
        result.push(createSummaryItem(translations['for'], 'grey'))
      }
      result.push(createSummaryItem(translations['project '], 'grey'))
      result.push(createSummaryItem(value_project))
    } else if (!value_filter && ['Timesheet', 'PieChart'].indexOf(pivotTableType) < 0) {
      if (result.length > 1) {
        result.push(createSummaryItem(translations['and'], 'grey'))
      } else {
        result.push(createSummaryItem(translations['for'], 'grey'))
      }
      result.push(createSummaryItem(translations['all projects']))
    }
    if (value_filter != null && value_filter.length != '') {
      if (result.length > 1) {
        result.push(createSummaryItem(translations['and'], 'grey'))
      }
      result.push(createSummaryItem(translations['filter'], 'grey'))
      result.push(createSummaryItem(value_filter))
    }
    if (value_groupedBy != null && value_groupedBy != '') {
      result.push(createSummaryItem(translations['grouped by'], 'grey'))
      result.push(createSummaryItem(value_groupedBy))
    }
    if (value_categorizeBy != null && value_categorizeBy != '') {
      result.push(createSummaryItem(translations['categorized by'], 'grey'))
      result.push(createSummaryItem(value_categorizeBy))
    }
    if (value_startDate != null && value_startDate != '') {
      result.push(createSummaryItem(translations['from'], 'grey'))
      result.push(createSummaryItem(value_startDate))
    }
    if (value_endDate != null && value_endDate != '') {
      result.push(createSummaryItem(translations['to'], 'grey'))
      result.push(createSummaryItem(value_endDate))
    }
    return result
  }
}
